body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background: black;
  background-image: url(/star-wars/static/media/back.8c155502.jpg);
  background-size: 100vw auto;
  background-repeat: no-repeat;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.searchBox {
  height: 2em;
  /* border: 2px solid red; */
  display: -webkit-flex;
  display: flex;    
  -webkit-justify-content: center;    
          justify-content: center;  
}

h1 {
  margin-top: 5px;
  margin-bottom: 5px;
  font-family: "STARWARS";
  -webkit-text-fill-color: black; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: yellow;
  font-size: 15vh;
  /* border: 2px solid red; */
}

@font-face {font-family:"STARWARS";src:url(/star-wars/static/media/STARWARS.35f31c7a.woff) format("woff");font-weight:normal;font-style:normal;}


.scrollContainer {
    overflow-y: scroll; 
    height: 62vh;
    margin-top: 10vh;
    border: 1px solid white;
    border-radius: 20px;
    margin-left: 20px;
    margin-right: 20px;
}
.block {
    width: 40px;
    height: 20px;
    /* background:white; */
    border: 1px solid black;
    margin: 0px 0px 0px 0px;
    padding: 0px 0px 0px 0px;
    font-size: .7em;
    text-align: center;
}
.card {
    -webkit-transition: all .3s;
    transition: all .3s;
    box-shadow: none;
    border: 3px solid grey;
    background: rgba(200, 200, 200, .25);
    border-radius: 20px;
    display: grid;
    width: 23em;
    height: 10em;
    grid-template-columns: 55% 31%;
    grid-template-rows: repeat(4, 20%);
    grid-auto-flow: column;
    grid-gap: 8px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-content: center;
            align-content: center;
    margin: 10px 0px 10px 0px;
}

.card:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border: 3px solid white;
    background: rgba(200, 200, 200, .85);
}

.line {
    /* border: 1px solid red; */
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

p {
    -webkit-align-items: center;
            align-items: center;
    font-weight: bold
}
.cards {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;    
}
